function sliderWorks() {
    new Swiper(".works .mySwiper",{
        slidesPerView: 4,
        spaceBetween: 8,
        grid: {
            rows: 2,
        },
        navigation: {
            nextEl: ".works .swiper-button-next",
            prevEl: ".works .swiper-button-prev"
        },
        pagination: {
            el: ".works .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                grid: {
                    rows: 1,
                },
            },
            577: {
                slidesPerView: 2,
                grid: {
                    rows: 1,
                },
            },
            769: {
                slidesPerView: 3,
                grid: {
                    rows: 1,
                },
            },
            993: {
                slidesPerView: 4,
                grid: {
                    rows: 2,
                },
            }
        }
    })
}

$(document).ready(function() {
    sliderWorks();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Numbers
    $(document).ready(function () {
        var a = 0;
        $(window).scroll(function () {
            var oTop = $(".advantages__items2").offset().top - window.innerHeight;
            if (a == 0 && $(window).scrollTop() > oTop) {
                $(".counter-value").each(function () {
                    var $this = $(this),
                        countTo = $this.attr("data-count");
                    $({
                        countNum: $this.text(),
                    }).animate(
                        {
                            countNum: countTo,
                        },
                        {
                            duration: 2000,
                            easing: "swing",
                            step: function () {
                                $this.text(Math.floor(this.countNum));
                            },
                            complete: function () {
                                $this.text(this.countNum);
                            },
                        }
                    );
                });
                a = 1;
            }
        });
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        // $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    function closeModal() {
        $('.modal').removeClass("modal--active")
        // $("body").removeClass("fixed-body")
    }

})